/**
 * 文件处理工具类
 */

// 文件大小格式化
export function formatFileSize(bytes) {
  if (bytes === 0) return '0 B'
  const k = 1024
  const sizes = ['B', 'KB', 'MB', 'GB']
  const i = Math.floor(Math.log(bytes) / Math.log(k))
  return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
}

// 检查文件类型
export function checkFileType(file, allowedTypes) {
  const fileExtension = '.' + file.name.split('.').pop().toLowerCase()
  return allowedTypes.includes(fileExtension)
}

// 检查文件大小
export function checkFileSize(file, maxSize = 100 * 1024 * 1024) { // 默认100MB
  return file.size <= maxSize
}

// 获取文件扩展名
export function getFileExtension(filename) {
  return filename.split('.').pop().toLowerCase()
}

// 生成唯一文件名
export function generateUniqueFileName(originalName) {
  const timestamp = Date.now()
  const random = Math.random().toString(36).substring(2, 8)
  const extension = getFileExtension(originalName)
  return `${timestamp}_${random}.${extension}`
}

// 下载文件
export function downloadFile(blob, filename) {
  const url = window.URL.createObjectURL(blob)
  const link = document.createElement('a')
  link.href = url
  link.download = filename
  document.body.appendChild(link)
  link.click()
  document.body.removeChild(link)
  window.URL.revokeObjectURL(url)
}

// 预览图片
export function previewImage(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader()
    reader.onload = e => resolve(e.target.result)
    reader.onerror = reject
    reader.readAsDataURL(file)
  })
}

// 支持的文件类型
export const FILE_TYPES = {
  PDF: ['.pdf'],
  IMAGE: ['.jpg', '.jpeg', '.png', '.bmp', '.tiff'],
  WORD: ['.doc', '.docx'],
  EXCEL: ['.xls', '.xlsx'],
  POWERPOINT: ['.ppt', '.pptx']
}

// 文件类型验证
export function validateFile(file, type) {
  const errors = []
  
  if (!checkFileType(file, FILE_TYPES[type])) {
    errors.push(`文件类型不支持，请上传${FILE_TYPES[type].join('、')}格式的文件`)
  }
  
  if (!checkFileSize(file)) {
    errors.push('文件大小不能超过100MB')
  }
  
  return {
    valid: errors.length === 0,
    errors
  }
}
